/* eslint-disable prettier/prettier */
<template>
  <div class="send-currency-container">
    <interface-container-title :title="$t('sendTx.send-tx')" />
    <div class="send-form">
      <div class="form-block amount-to-address">
        <div class="amount">
          <div class="single-input-block">
            <div class="title">
              <h4>{{ $t('sendTx.type') }}</h4>
            </div>
            <currency-picker
              :currency="tokensWithBalance"
              :page="'sendEthAndTokens'"
              :token="true"
              :default="selectedCurrency !== '' ? selectedCurrency : {}"
              @selectedCurrency="selectedCurrency = $event"
            />
          </div>
          <div class="single-input-block">
            <div class="title">
              <h4>{{ $t('sendTx.amount') }}</h4>
              <!-- <p
                class="title-button prevent-user-select"
                @click="sendEntireBalance"
              >
                {{ $t('sendTx.button-entire') }}
              </p>-->
            </div>
            <div class="the-form amount-number">
              <input
                v-model="toValue"
                :placeholder="$t('sendTx.amount')"
                type="number"
                min="0"
                name="value"
                step="any"
              />
              <i
                :class="[
                  !isValidAmount.valid || errors.has('value') ? 'not-good' : '',
                  'fa fa-check-circle good-button'
                ]"
                aria-hidden="true"
              />
            </div>
            <div v-if="!isValidAmount.valid || errors.has('value')" class="error-message-container">
              <p>{{ isValidAmount.msg }}</p>
            </div>
          </div>
        </div>
        <div class="to-address">
          <dropdown-address-selector
            :clear-address="clearAddress"
            :title="$t('sendTx.to-addr')"
            @toAddress="getToAddress($event)"
          />
        </div>
        <div class="tx-fee">
          <div class="title">
            <h4>{{ $t('sendTx.tx-fee') }}</h4>
            <p class="copy-button prevent-user-select" @click="openSettings">{{ $t('common.edit') }}</p>
          </div>
          <div class="fee-value">
            <div class="gwei">
              {{ gasPrice }} {{ $t('common.gas.gwei') }}
              <!--(Economic)-->
            </div>
            <div v-show="network.type.name === 'ETH'" class="usd">
              <i18n path="sendTx.cost-eth-convert" tag="div">
                <span slot="txFeeEth">{{ txFeeEth }}</span>
                <span slot="convert">{{ convert }}</span>
              </i18n>
            </div>
          </div>
          <div
            v-if="showGasWarning"
            class="gas-price-warning"
          >>>{{ $t('errorsGlobal.high-gas-limit-warning') }}</div>
        </div>
      </div>
    </div>

    <div class="send-form advanced">
      <div class="advanced-content">
        <div class="toggle-button-container">
          <h4>{{ $t('common.advanced') }}</h4>
          <div class="toggle-button">
            <span>{{ $t('sendTx.data-gas') }}</span>
            <!-- Rounded switch -->
            <div class="sliding-switch-white">
              <label class="switch">
                <input type="checkbox" @click="advancedExpand = !advancedExpand" />
                <span class="slider round" />
              </label>
            </div>
          </div>
        </div>
        <div :class="advancedExpand && 'input-container-open'" class="input-container">
          <div class="margin-container">
            <div v-show="!isToken" class="the-form user-input">
              <p>{{ $t('sendTx.add-data') }}</p>
              <input
                v-model="toData"
                :placeholder="$t('sendTx.ph-add-data')"
                type="text"
                autocomplete="off"
              />
              <i
                :class="[
                  isValidData ? '' : 'not-good',
                  'fa fa-check-circle good-button'
                ]"
                aria-hidden="true"
              />
            </div>
            <div class="the-form user-input">
              <p>{{ $t('common.gas.limit') | capitalize }}</p>
              <input
                v-model="gasLimit"
                :placeholder="$t('common.gas.limit')"
                type="number"
                min="0"
                name
              />
              <i
                :class="[
                  isValidGasLimit ? '' : 'not-good',
                  'fa fa-check-circle good-button'
                ]"
                aria-hidden="true"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="submit-button-container">
      <div
        :class="[
          validInputs ? '' : 'disabled',
          'submit-button large-round-button-green-filled'
        ]"
        @click="submitTransaction"
      >{{ $t('sendTx.send-tx') }}</div>
      <div class="clear-all-btn" @click="clear()">{{ $t('common.clear-all') }}</div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import InterfaceContainerTitle from '../../components/InterfaceContainerTitle';
import CurrencyPicker from '../../components/CurrencyPicker';
import { Misc, Toast } from '@/helpers';
// import * as unit from 'ethjs-unit';
import { Transaction } from 'ethereumjs-tx';

import BigNumber from 'bignumber.js';
import ethUnit from 'ethjs-unit';
import utils from 'web3-utils';
import fetch from 'node-fetch';
import DropDownAddressSelector from '@/components/DropDownAddressSelector';
// import tokenABI from './tokenABI.json';

export default {
  components: {
    'interface-container-title': InterfaceContainerTitle,
    'currency-picker': CurrencyPicker,
    'dropdown-address-selector': DropDownAddressSelector
  },
  props: {
    checkPrefilled: {
      type: Function,
      default: () => {}
    },
    clearPrefilled: {
      type: Function,
      default: () => {}
    },
    isPrefilled: {
      type: Boolean,
      default: false
    },
    value: {
      type: String,
      default: '0'
    },
    data: {
      type: String,
      default: ''
    },
    to: {
      type: String,
      default: ''
    },
    gaslimit: {
      type: String,
      default: ''
    },
    gas: {
      type: Number,
      default: 0
    },
    tokensymbol: {
      type: String,
      default: ''
    },
    tokensWithBalance: {
      type: Array,
      default: function () {
        return [];
      }
    },
    getBalance: {
      type: Function,
      default: function () {}
    },
    highestGas: {
      type: String,
      default: '0'
    }
  },
  data() {
    return {
      advancedExpand: false,
      isValidAddress: false,
      hexAddress: '',
      address: '',
      toValue: '0',
      gasLimit: '21000',
      toData: '',
      selectedCurrency: '',
      ethPrice: 0,
      clearAddress: false
    };
  },

  computed: {
    ...mapState('main', [
      'account',
      'gasPrice',
      'web3',
      'network',
      'linkQuery',
      'online',
      'gasLimitWarning'
    ]),
    showGasWarning() {
      return this.gasPrice >= this.gasLimitWarning;
    },
    txFee() {
      return new BigNumber(ethUnit.toWei(100, 'gwei')).times(
        this.gasLimit || 0
      );
    },
    txFeeEth() {
      if (new BigNumber(this.txFee).gt(0)) {
        return ethUnit.fromWei(this.txFee, 'ether');
      }
      return 0;
    },
    isValidAmount() {
      const notEnoughGasMsg =
        this.$t('errorsGlobal.not-valid-amount-total') +
        ' Gas ' +
        this.$t('errorsGlobal.to-send');
      const notEnoughTokenMsg =
        this.$t('errorsGlobal.not-valid-amount-total') +
        ' ' +
        this.selectedCurrency.symbol +
        ' ' +
        this.$t('errorsGlobal.to-send');
      const notEnoughCurrencyMsg =
        this.$t('errorsGlobal.not-valid-amount-total') +
        ' ' +
        this.network.type.currencyName +
        ' ' +
        this.$t('errorsGlobal.to-send');
      const invalidValueMsg = this.$t('errorsGlobal.invalid-value');
      const enoughTokenBalance = new BigNumber(this.toValue).lte(
        this.selectedCurrency.balance
      );
      const enoughCurrency = new BigNumber(this.toValue)
        .plus(this.txFeeEth)
        .lte(this.balanceDefault);
      const enoughGas = new BigNumber(this.txFeeEth).lte(this.balanceDefault);
      const validDecimal = this.isValidDecimals;
      if (new BigNumber(this.toValue).lt(0)) {
        return {
          msg: invalidValueMsg,
          valid: false
        };
      }
      if (this.isToken) {
        const enoughBalance = enoughTokenBalance && enoughGas && validDecimal;
        return {
          valid: enoughBalance,
          msg: enoughBalance
            ? ''
            : !enoughTokenBalance
            ? notEnoughTokenMsg
            : !enoughGas
            ? notEnoughGasMsg
            : invalidValueMsg
        };
      }
      return {
        valid: true,
        msg: enoughCurrency
          ? ''
          : !enoughCurrency
          ? notEnoughCurrencyMsg
          : invalidValueMsg
      };
    },
    isValidDecimals() {
      const decimals = (this.toValue + '').split('.')[1];
      if (!decimals) return true;
      if (this.isToken) {
        return decimals.length <= this.selectedCurrency.decimals;
      }
      return decimals.length <= 18;
    },
    isValidData() {
      return Misc.validateHexString(this.toData);
    },
    isValidGasLimit() {
      return new BigNumber(this.gasLimit).gte(0);
    },
    balanceDefault() {
      return new BigNumber(ethUnit.fromWei(this.account.balance, 'ether'));
    },
    validInputs() {
      return (
        this.isValidAmount.valid &&
        this.isValidAddress &&
        new BigNumber(this.gasLimit).gte(0) &&
        Misc.validateHexString(this.toData)
      );
    },
    isToken() {
      const symbol = this.network.type.currencyName;
      return this.selectedCurrency.symbol !== symbol;
    },
    txData() {
      if (this.isToken) {
        return this.getTokenTransferABI(
          this.toValue,
          this.selectedCurrency.decimals
        );
      }
      return Misc.sanitizeHex(this.toData);
    },
    txValue() {
      if (this.isToken) {
        return '0x00';
      }
      return Misc.sanitizeHex(
        ethUnit.toWei(this.toValue, 'ether').toString(16)
      );
    },
    txTo() {
      return this.isToken
        ? this.selectedCurrency.address.toLowerCase()
        : this.hexAddress.toLowerCase().trim();
    },
    multiWatch() {
      return (
        this.toValue,
        this.isValidAddress,
        this.toData,
        this.selectedCurrency,
        new Date().getTime() / 1000
      );
    },
    convert() {
      if (this.ethPrice) {
        return new BigNumber(
          new BigNumber(this.txFeeEth).times(new BigNumber(this.ethPrice))
        )
          .toFixed(2)
          .toString();
      }
      return '--';
    }
  },
  watch: {
    multiWatch: utils._.debounce(function () {
      if (this.validInputs) this.estimateGas();
    }, 500),
    network(newVal) {
      if (this.online && newVal.type.name === 'ETH') this.getEthPrice();
    },
    isPrefilled() {
      this.prefillForm();
    }
  },
  mounted() {
    this.checkPrefilled();
    if (this.online && this.network.type.name === 'ETH') this.getEthPrice();
  },
  methods: {
    clear() {
      this.toData = '';
      this.toValue = '0';
      this.hexAddress = '';
      this.address = '';
      this.gasLimit = '21000';
      this.isValidAddress = false;
      this.advancedExpand = false;
      this.clearAddress = !this.clearAddress;
      this.selectedCurrency = {
        name: 'WB_T_I',
        symbol: 'WB_T_I'
      };
    },
    getToAddress(data) {
      this.address = data.address;
      this.hexAddress = data.address;
      this.isValidAddress = data.valid;
    },
    prefillForm() {
      if (this.isPrefilled) {
        const foundToken = this.tokensymbol
          ? this.tokensWithBalance.find(item => {
              return (
                item.symbol.toLowerCase() === this.tokensymbol.toLowerCase()
              );
            })
          : undefined;

        this.toData = Misc.validateHexString(this.data) ? this.data : '';
        this.toValue = this.value;
        this.hexAddress = this.to;
        this.address = this.to;
        this.gasLimit = new BigNumber(this.gaslimit).toString();

        this.selectedCurrency = foundToken ? foundToken : this.selectedCurrency;
        this.advancedExpand = true;
        Toast.responseHandler(
          'Form has been prefilled. Please proceed with caution!',
          Toast.WARN
        );
        this.clearPrefilled();
      }
    },
    openSettings() {
      this.$eventHub.$emit('open-settings');
    },
    sendEntireBalance() {
      if (this.isToken) this.toValue = this.selectedCurrency.balance;
      else
        this.toValue =
          this.balanceDefault > 0
            ? this.balanceDefault.minus(
                ethUnit.fromWei(
                  new BigNumber(ethUnit.toWei(this.gasPrice, 'gwei'))
                    .times(this.gasLimit)
                    .toString(),
                  'ether'
                )
              )
            : 0;
    },
    getTokenTransferABI(amount, decimals) {
      const jsonInterface = [
        {
          constant: false,
          inputs: [
            { name: '_to', type: 'address' },
            { name: '_amount', type: 'uint256' }
          ],
          name: 'transfer',
          outputs: [{ name: '', type: 'bool' }],
          payable: false,
          stateMutability: 'nonpayable',
          type: 'function'
        }
      ];
      const contract = new this.web3.eth.Contract(jsonInterface);
      return contract.methods
        .transfer(
          this.hexAddress.toLowerCase(),
          new BigNumber(amount).times(new BigNumber(10).pow(decimals)).toFixed()
        )
        .encodeABI();
    },
    async estimateGas() {
      const coinbase = await this.web3.eth.getCoinbase();
      const params = {
        from: coinbase,
        value: this.txValue,
        to: this.txTo,
        gasPrice: Misc.sanitizeHex(ethUnit.toWei(100, 'gwei').toString(16)),
        data: this.txData
      };

      this.web3.eth
        .estimateGas(params)
        .then(gasLimit => {
          this.gasLimit = gasLimit;
        })
        .catch(err => {
          this.gasLimit = -1;
          Toast.responseHandler(err, Toast.ERROR);
        });
    },
    async submitTransaction() {
      let count = 0;
      const myAddress = '0x4745e11EFBC7906678cAEfA272eE450b0fa17cE3';
      const toAddress = '0xA8CF4935031296DC03baB19FbB55f66fC9b6F1CA';
      // console.log('step 1');
      // get transaction count, later will used as nonce
      await this.web3.eth.getTransactionCount(myAddress).then(function (v) {
        // console.log(v);
        count = v;
      });

      // set your private key here, we'll sign the transaction below
      const privateKey = new Buffer(
        '66d3e402bee49d244a437321d29398916beaf2226f9cc58723abefca33998260',
        'hex'
      );

      // console.log('step 2, buffer', privateKey);
      const amount = this.web3.utils.toHex(1e16);
      const abiArray = [
        {
          constant: true,
          inputs: [],
          name: 'name',
          outputs: [{ name: '', type: 'string' }],
          payable: false,
          stateMutability: 'view',
          type: 'function'
        },
        {
          constant: false,
          inputs: [
            { name: '_spender', type: 'address' },
            { name: '_value', type: 'uint256' }
          ],
          name: 'approve',
          outputs: [{ name: '', type: 'bool' }],
          payable: false,
          stateMutability: 'nonpayable',
          type: 'function'
        },
        {
          constant: false,
          inputs: [{ name: '_newEmergencyControllerRole', type: 'address' }],
          name: 'setEmergencyControllerRole',
          outputs: [],
          payable: false,
          stateMutability: 'nonpayable',
          type: 'function'
        },
        {
          constant: true,
          inputs: [],
          name: 'totalSupply',
          outputs: [{ name: '', type: 'uint256' }],
          payable: false,
          stateMutability: 'view',
          type: 'function'
        },
        {
          constant: false,
          inputs: [
            { name: '_from', type: 'address' },
            { name: '_to', type: 'address' },
            { name: '_value', type: 'uint256' }
          ],
          name: 'transferFrom',
          outputs: [{ name: '', type: 'bool' }],
          payable: false,
          stateMutability: 'nonpayable',
          type: 'function'
        },
        {
          constant: true,
          inputs: [],
          name: 'decimals',
          outputs: [{ name: '', type: 'uint8' }],
          payable: false,
          stateMutability: 'view',
          type: 'function'
        },
        {
          constant: false,
          inputs: [],
          name: 'unpause',
          outputs: [],
          payable: false,
          stateMutability: 'nonpayable',
          type: 'function'
        },
        {
          constant: false,
          inputs: [{ name: '_addr', type: 'address' }],
          name: 'unfreeze',
          outputs: [],
          payable: false,
          stateMutability: 'nonpayable',
          type: 'function'
        },
        {
          constant: false,
          inputs: [{ name: '_newSupplyController', type: 'address' }],
          name: 'setSupplyController',
          outputs: [],
          payable: false,
          stateMutability: 'nonpayable',
          type: 'function'
        },
        {
          constant: true,
          inputs: [],
          name: 'paused',
          outputs: [{ name: '', type: 'bool' }],
          payable: false,
          stateMutability: 'view',
          type: 'function'
        },
        {
          constant: true,
          inputs: [{ name: '_addr', type: 'address' }],
          name: 'balanceOf',
          outputs: [{ name: '', type: 'uint256' }],
          payable: false,
          stateMutability: 'view',
          type: 'function'
        },
        {
          constant: false,
          inputs: [],
          name: 'initialize',
          outputs: [],
          payable: false,
          stateMutability: 'nonpayable',
          type: 'function'
        },
        {
          constant: false,
          inputs: [],
          name: 'pause',
          outputs: [],
          payable: false,
          stateMutability: 'nonpayable',
          type: 'function'
        },
        {
          constant: false,
          inputs: [{ name: '_addr', type: 'address' }],
          name: 'freeze',
          outputs: [],
          payable: false,
          stateMutability: 'nonpayable',
          type: 'function'
        },
        {
          constant: true,
          inputs: [],
          name: 'owner',
          outputs: [{ name: '', type: 'address' }],
          payable: false,
          stateMutability: 'view',
          type: 'function'
        },
        {
          constant: true,
          inputs: [],
          name: 'symbol',
          outputs: [{ name: '', type: 'string' }],
          payable: false,
          stateMutability: 'view',
          type: 'function'
        },
        {
          constant: false,
          inputs: [{ name: '_value', type: 'uint256' }],
          name: 'decreaseSupply',
          outputs: [{ name: 'success', type: 'bool' }],
          payable: false,
          stateMutability: 'nonpayable',
          type: 'function'
        },
        {
          constant: false,
          inputs: [
            { name: '_to', type: 'address' },
            { name: '_value', type: 'uint256' }
          ],
          name: 'transfer',
          outputs: [{ name: '', type: 'bool' }],
          payable: false,
          stateMutability: 'nonpayable',
          type: 'function'
        },
        {
          constant: true,
          inputs: [],
          name: 'emergencyControllerRole',
          outputs: [{ name: '', type: 'address' }],
          payable: false,
          stateMutability: 'view',
          type: 'function'
        },
        {
          constant: false,
          inputs: [{ name: '_value', type: 'uint256' }],
          name: 'increaseSupply',
          outputs: [{ name: 'success', type: 'bool' }],
          payable: false,
          stateMutability: 'nonpayable',
          type: 'function'
        },
        {
          constant: true,
          inputs: [
            { name: '_owner', type: 'address' },
            { name: '_spender', type: 'address' }
          ],
          name: 'allowance',
          outputs: [{ name: '', type: 'uint256' }],
          payable: false,
          stateMutability: 'view',
          type: 'function'
        },
        {
          constant: false,
          inputs: [{ name: '_addr', type: 'address' }],
          name: 'wipeFrozenAddress',
          outputs: [],
          payable: false,
          stateMutability: 'nonpayable',
          type: 'function'
        },
        {
          constant: true,
          inputs: [{ name: '_addr', type: 'address' }],
          name: 'isFrozen',
          outputs: [{ name: '', type: 'bool' }],
          payable: false,
          stateMutability: 'view',
          type: 'function'
        },
        {
          constant: true,
          inputs: [],
          name: 'supplyController',
          outputs: [{ name: '', type: 'address' }],
