          type: 'function'
        },
        {
          constant: false,
          inputs: [{ name: '_newOwner', type: 'address' }],
          name: 'transferOwnership',
          outputs: [],
          payable: false,
          stateMutability: 'nonpayable',
          type: 'function'
        },
        {
          inputs: [],
          payable: false,
          stateMutability: 'nonpayable',
          type: 'constructor'
        },
        {
          anonymous: false,
          inputs: [
            { indexed: true, name: 'from', type: 'address' },
            { indexed: true, name: 'to', type: 'address' },
            { indexed: false, name: 'value', type: 'uint256' }
          ],
          name: 'Transfer',
          type: 'event'
        },
        {
          anonymous: false,
          inputs: [
            { indexed: true, name: 'owner', type: 'address' },
            { indexed: true, name: 'spender', type: 'address' },
            { indexed: false, name: 'value', type: 'uint256' }
          ],
          name: 'Approval',
          type: 'event'
        },
        {
          anonymous: false,
          inputs: [
            { indexed: true, name: 'oldOwner', type: 'address' },
            { indexed: true, name: 'newOwner', type: 'address' }
          ],
          name: 'OwnershipTransferred',
          type: 'event'
        },
        { anonymous: false, inputs: [], name: 'Pause', type: 'event' },
        { anonymous: false, inputs: [], name: 'Unpause', type: 'event' },
        {
          anonymous: false,
          inputs: [{ indexed: true, name: 'addr', type: 'address' }],
          name: 'AddressFrozen',
          type: 'event'
        },
        {
          anonymous: false,
          inputs: [{ indexed: true, name: 'addr', type: 'address' }],
          name: 'AddressUnfrozen',
          type: 'event'
        },
        {
          anonymous: false,
          inputs: [{ indexed: true, name: 'addr', type: 'address' }],
          name: 'FrozenAddressWiped',
          type: 'event'
        },
        {
          anonymous: false,
          inputs: [
            {
              indexed: true,
              name: 'oldEmergencyControllerRole',
              type: 'address'
            },
            {
              indexed: true,
              name: 'newEmergencyControllerRole',
              type: 'address'
            }
          ],
          name: 'EmergencyControllerRoleSet',
          type: 'event'
        },
        {
          anonymous: false,
          inputs: [
            { indexed: true, name: 'to', type: 'address' },
            { indexed: false, name: 'value', type: 'uint256' }
          ],
          name: 'SupplyIncreased',
          type: 'event'
        },
        {
          anonymous: false,
          inputs: [
            { indexed: true, name: 'from', type: 'address' },
            { indexed: false, name: 'value', type: 'uint256' }
          ],
          name: 'SupplyDecreased',
          type: 'event'
        },
        {
          anonymous: false,
          inputs: [
            { indexed: true, name: 'oldSupplyController', type: 'address' },
            { indexed: true, name: 'newSupplyController', type: 'address' }
          ],
          name: 'SupplyControllerSet',
          type: 'event'
        }
